var React = require('react');
require('./styles.less');

function BuscadorEjes(props){
  return (
    <section id='buscador-ejes'>

      <div className='form-group'>
        <label htmlFor='input_nivel'>Nivel:</label>
        <select name='input_nivel'>
          <option>Primaria</option>
          <option>Secundaria</option>
        </select>
      </div>

      <div className='form-group'>
        <label htmlFor='input_grado'>Grado:</label>
        <select name='input_grado'>
          <option>1 ao EP</option>
          <option>1 ao ES</option>
          <option>2 ao EP</option>
          <option>2 ao ES</option>
          <option>3 ao EP</option>
          <option>3 ao ES</option>
          <option>4 ao EP</option>
          <option>4 ao ES</option>
          <option>5 ao EP</option>
          <option>5 ao ES</option>
          <option>6 ao EP</option>
          <option>6 ao ES</option>
        </select>
      </div>

    </section>
  )
}

module.exports = BuscadorEjes;



